.App {
  text-align: ;
}

.App-logo {
  height: 60px;
  pointer-events: none;
}

.App-header {
  background-color: #3c98c6;
  min-height: 40px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  color: white;
}
.App-header nav{

}
.App-header nav li{
  list-style-type: none;
  display: inline-flex;
  padding: 0.4em 0.8em;
  font-size: 16px;
  color: #FFF;
}
.App-header nav li a{
  color: #fff !important;
  text-decoration: none;
  cursor: pointer;
}
.App-header nav li a:hover{
  color:#61dafb !important;
}
section{
  margin-top: 4rem;
  text-align: center;
  padding: 0 1em;
}
h1{font-size: 44px; }
p{
  font-size: 16px;
  line-height: 22px;
}
.login{
  padding-top: 3rem;
  padding-bottom: 2em;
  background: #e5e5e5;
  height:88vh;
}
.signup{
  padding-top: 1rem;
  padding-bottom: 2em;
  background: #e5e5e5;
  height:94vh;
}
.login h3, .signup h3{font-size: 20px; text-align: center;}
.login p, .signup p{ font-size: 16px; text-align: center;}
form{
  padding: 2em 10px;
  background: #fff;
  width: 500px;
  margin: 2em auto;
}
.form-group{
  margin: 15px 1em;
}
.form-control{
  height: 40px;
  border: 1px solid #526173;
  border-radius: none;
  width: 100%;
  padding-left: 10px;
}
.m-auto{margin: auto;}
button{
  padding: 0.7em 2.8em;
  color: #fff;
  background: #3c98c6;
  font-size: 15px;
  border: none;
  margin-top: 1em;
  margin-left: 10rem;
  cursor: pointer;
}